
import { defineComponent } from "vue";
import axios from "axios";

interface Data {
  user: {
    name: string;
    email: string;
    password: string;
    phone: string;
    city: string;
    state: string;
    cep: string;
  };
  errors: string[];
}

export default defineComponent({
  name: "Criar Conta",
  data(): Data {
    return {
      user: {
        name: "",
        email: "",
        password: "",
        phone: "",
        city: "",
        state: "",
        cep: ""
      },
      errors: []
    };
  },
  methods: {
    handleSubmit(e: HTMLFormElement) {
      e.preventDefault();

      this.errors = [];

      if (!this.user.name) {
        this.errors.push("O nome deve ser preenchido.");
      }
      if (!this.user.email) {
        this.errors.push("O e-mail deve ser preenchido.");
      }
      if (!this.user.password) {
        this.errors.push("A senha deve ser preenchida.");
      }
      if (!this.user.phone) {
        this.errors.push("O telefone deve ser preenchido.");
      }
      if (!this.user.city) {
        this.errors.push("A cidade deve ser preenchida.");
      }
      if (!this.user.state) {
        this.errors.push("O estado deve ser preenchido.");
      }
      if (!this.user.cep) {
        this.errors.push("O CEP deve ser preenchido.");
      }

      if (this.errors.length) {
        return;
      }

      axios
        .post("http://localhost:3000/users", this.user)
        .then(() => this.$router.push("/login"))
        .catch(e => this.errors.push(e.message));
    }
  }
});
